import React, { Component } from 'react'
import "./CreateDir.css"

export default class Create extends Component {
    state={
        name_duplication:false,
    }
    componentDidMount(){
        this.inputNode.focus()
    }
    keyDown=(event)=>{
        if(event.keyCode===13){
            this.sure()
        }
    }
    sure=()=>{
        if(!this.name_duplication){
            const name=this.inputNode.value.trim()
            this.props.createNewDir(name)
            this.cancel()
        }
    }
    cancel=()=>{
        this.props.cancelNewDir()
    }
    blur=()=>{
        this.name_duplication=false
        const name=this.inputNode.value.trim()
        const index=this.props.currentList.findIndex(item => item.name===name);
        if(index!==-1){//重名
            this.name_duplication=true
            this.setState({name_duplication:true})
        }else{
            this.setState({name_duplication:false})
        }
    }
    render() {
        return (
            // <div className="createdir_mask">
            <div className="createdir">
                <div className="create_inputouter">
                    <input 
                    className="create_inputinner" 
                    placeholder="请输入新文件夹名字" 
                    type="text" 
                    ref={node=>{this.inputNode=node}} 
                    onKeyDown={this.keyDown} 
                    onBlur={this.blur}
                    />
                    <span className="create_warn" style={{display:this.state.name_duplication ? "block" : "none"}}>
                        重名
                    </span>
                </div>
                <button className="create_cancel" onClick={this.cancel}>取消</button>
                <button className="create_sure" onClick={this.sure}>确定</button>
            </div>
            // </div>

        )
    }
}
