/**
 * EpisodeList.js
 *
 * This is just a plain ol' React component.
 * the vjsComponent methods, player methods etc. are available via
 * the vjsComponent prop (`this.props.vjsComponent`)
 */
import React, { Component,  } from 'react';//PropTypes
import ReactDOM from 'react-dom';
import videojs from 'video.js';
import "./NextEpisode.css"

class NextEpisode extends Component {
    render() {
        // console.log(this.props.body.nextep);
        return (
            <div className="nextepisode" onClick={this.props.body.nextep}>
                <svg width="100%" height="100%" viewBox="0 0 99 99">
                    <polyline points="33 33 64 49.5 33 66" fill="white" />
                    <line x1="66" y1="33" x2="66" y2="66" stroke="white" strokeWidth="4" />
                </svg>
            </div>
        );
    }
}


/**
 * vjsEpisodeList.js
 * Here is where we register a Video JS Component and
 * mount the React component to it when the player is ready.
 */
//  import EpisodeList from './EpisodeList';


const vjsComponent = videojs.getComponent('Component');

class vjsNextEpisode extends vjsComponent {

    constructor(player, options) {
        super(player, options);
        /* Bind the current class context to the mount method */
        this.mount = this.mount.bind(this);
        this.data = options
        /* When player is ready, call method to mount React component */
        player.ready(() => {
            this.mount();
        });

        /* Remove React root when component is destroyed */
        this.on("dispose", () => {
            ReactDOM.unmountComponentAtNode(this.el())
        });
    }

    /**
     * We will render out the React EpisodeList component into the DOM element
     * generated automatically by the VideoJS createEl() method.
     *
     * We fetch that generated element using `this.el()`, a method provided by the
     * vjsComponent class that this class is extending.
     */
    mount() {
        ReactDOM.render(<NextEpisode vjsComponent={this} body={this.data} />, this.el());
    }
}

/**
 * Make sure to register the vjsComponent so Video JS knows it exists
 */
vjsComponent.registerComponent('vjsNextEpisode', vjsNextEpisode);

export default vjsNextEpisode;
