import React, { Component } from 'react'
// import { Swiper, SwiperSlide } from 'swiper/react';
// import 'swiper/swiper.scss';
import pubsub from "pubsub-js"
import "./ButtonSE.css"


export default class ButtonSE extends Component {
    state={
        s_swift:1,
        e:1,
        s:1,
    }
    sete=(_,e)=>{
        this.setState({e})
    }
    componentDidMount(){
        pubsub.subscribe("sete",this.sete)
    }
    componentWillUnmount(){
        pubsub.unsubscribe("sete")
    }
    create_s_e=()=>{
        let { type, bundleSize } = this.props.videoInfo
        const seasonCN=["第一季","第二季","第三季","第四季","第五季","第六季","第七季","第八季","第九季","第十季","第十一季","第十二季","第十三季","第十四季","第十五季"]
        const partCN=["第一部","第二部","第三部","第四部","第五部","第六部","第七部","第八部","第九部","第十部","第十一部","第十二部","第十三部","第十四部","第十五部"]
        let s_name=[]
        let s_e=[]
        let s_num=Object.keys(bundleSize).length
        // console.log(bundleSize,bundleKeys);
        if(s_num===1){
            let list=[]
            if(bundleSize['season_1']>1){
                for (let i=0;i<bundleSize['season_1']; i++){
                    if(type==="Movies"){
                        list.push(partCN[i])
                    }else{
                        list.push(i+1)
                    }
                }
            }
            s_e.push(list)
        }else{
            for (let j=1; j<=s_num; j++){
                s_name.push(seasonCN[j-1])
                let list=[]
                for (let i=0;i<bundleSize['season_'+j]*1; i++){
                    list.push(i+1)
                }
                s_e.push(list)
            }
        }
        this.s_e=s_e
        this.s_name=s_name
    }
    changeSeason=(index)=>{
        this.setState({s_swift:index+1})
    }
    changeEpisode=(index1, index2)=>{
        let s=index1+1
        let e=index2+1
        this.setState({s,e,})
        pubsub.publish("startep",{s,e})
    }
    createEpisode=(season,index1)=>{
        const {s_swift, e, s}=this.state
        return (

            <div 
            key={index1} 
            className="episode_swiper"
            style={{"display":((s_swift===(index1+1)) ? "flex" : "none")}}
            >
                {
                    season.map((ep,index2)=>(
                        <span
                        key={index2}
                        className={((e===(index2+1)) && (s===(index1+1))) ? "active_e_slide episode_slide" : "episode_slide"}
                        onClick={()=>{
                            // console.log(index1+1,s);
                            return this.changeEpisode(index1,index2)
                        }}
                        >{ep}</span>
                    ))
                }
            </div>
        )
    }
    render() {
        this.create_s_e()
        const {s_swift}=this.state
        return (
            <div>
                <div style={{"display":(this.s_e.length>1 ? "block" : "none")}}>
                    {this.s_name.map((name,index)=>(
                        <button 
                        key={index}
                        className={s_swift===(index+1) ? "active_s_button season_button" : "season_button"}
                        onClick={()=>this.changeSeason(index)}
                        >{name}</button>
                    ))}
                </div>
                {this.s_e.map(this.createEpisode)}
            </div>
        )
    }
}
