import React, { Component } from 'react'
import {Route, NavLink, Redirect, withRouter} from "react-router-dom"
// import CacheRoute, { CacheSwitch } from 'react-router-cache-route'
import "./App.css"
import SearchList from './paths/SearchList/SearchList'
import SearchButton from './components/SearchButton/SearchButton'
import Movie from './paths/Movie/Movie'
import Tv from "./paths/Tv/Tv"
import Animation from "./paths/Animation/Animation"
import PlayPage from './paths/PlayPage/PlayPage'
import File from "./paths/File/File"
// import Upload from './components/Upload/Upload'
import Recycle from "./paths/Recycle/Recycle"

class app extends Component {
    render() {
        return (
            <div className="app">
                <div className="appWraper">
                    <div className="appHeader">
                        <div className="applogo">
                            <span>闲</span>
                        </div>
                        <SearchButton />
                    </div>
                    <div className="appBody">
                        <div className="nav">
                            <NavLink className="link" activeClassName="linkActive" to="/movie" >电影</NavLink>
                            <NavLink className="link" activeClassName="linkActive" to="/animation" >动漫</NavLink>
                            <NavLink className="link" activeClassName="linkActive" to="/tv" >电视剧</NavLink>
                            <NavLink className="link" activeClassName="linkActive" to="/file">文件</NavLink>
                            <NavLink className="link" activeClassName="linkActive" to="/recycle">回收站</NavLink>
                        </div>
                        <div  className="routesContent">
                            <Route path="/movie" component={Movie} />
                            <Route path="/animation" component={Animation} />
                            <Route path="/tv" component={Tv} />
                            <Route path="/search" component={SearchList} />
                            <Route path="/playpage" component={PlayPage} />
                            <Route path="/file" component={File} />
                            <Route path="/recycle" component={Recycle} />
                            <Redirect to="/movie" />
                        </div>
                        
                    </div>
                    
                </div>
                
            </div>
        )

    }
}

export default withRouter(app)