import React, { Component } from 'react'
import axios from "axios"
import pubsub from "pubsub-js"
import {withRouter} from "react-router-dom"
import "./List.css"

class List extends Component {
    state={
        page:1,
        limit:30,
        contentlist:[],
        noMoreData:false,
    }
    componentDidMount(){
        pubsub.subscribe("getVideoList",this.getVideoList)
    }
    componentWillUnmount(){
        pubsub.unsubscribe("getVideoList")
    }
    getNextVideoList=()=>{
        const {type, tag}=this
        const {page, limit}=this.state
        if(page>this.pageGot){
            this.pageGot=this.pageGot+1
            axios.get(`/video/list?type=${type}&tag=${tag}&page=${page}&limit=${limit}`).then(res=>{
                let {contentlist, page}=this.state
                if(res.data.length>0){
                    this.setState({contentlist:[...contentlist,...res.data],page:page+1})
                }else{
                    this.setState({noMoreData:true})
                }
            })
        }
        
    }
    getVideoList=(_,{type, tag})=>{
        const {limit}=this.state
        this.type=type
        this.tag=tag
        this.pageGot=1
        axios.get(`/video/list?type=${type}&tag=${tag}&page=${1}&limit=${limit}`).then(res=>{
            if(res.data.length>0){
                this.setState({contentlist:res.data,page:2})
            }else{
                this.setState({noMoreData:true})
            }
        })
        
    }
    listScroll=(event)=>{
        let { scrollTop, offsetHeight, scrollHeight }=event.target//this.listWraperNode//
        let bottomHeight=scrollHeight-offsetHeight-scrollTop
        if(!this.state.noMoreData && bottomHeight<500){
            this.getNextVideoList()
        }
    }
    intoPlayer=(content)=>{
        const {_id:id, bundleName, bundleSize, type}=content
        this.props.history.push("/playpage",{id, bundleName,type,bundleSize})
    }
    coverImgFail=(event)=>{
        let src=event.target.src
        const index=src.lastIndexOf(".")
        const ext=src.slice(index+1,)
        console.log(ext);
        if(ext==="webp"){
            event.target.src=src.slice(0,index+1)+"jpg"
        }else if(ext==="jpg"){
            event.target.src=src.slice(0,index+1)+"png"
        }else{
            event.target.src="/imgs/defaultCover.jpg"
        }
    }
    render() {
        const {contentlist}=this.state
        return (
            <div className="listWraper" onScroll={this.listScroll} ref={listWraperNode=>this.listWraperNode=listWraperNode}>
                <ul className="list" >
                    {
                        contentlist.map(content=>{
                            return(
                                <li className="list_item" onClick={()=>{this.intoPlayer(content)}} key={content._id}>
                                    <img className="coverImg" src={`/imgs/${content.type}/${content.bundleName}.webp`} onError={this.coverImgFail} alt="" />
                                    <p title={content.bundleName} className="ellipsis videoName">{content.bundleName}</p>
                                </li>
                            )
                        })
                        
                    }

                </ul>
            </div>
        )
    }
}

export default withRouter(List)
