import React, { Component } from 'react'
import Video from '../../components/Video/Video'
// import "./Animation.css"
let  allTags =require("../../allTags")

export default class Animation extends Component {
    render() {
        const {animationTags, animationTagsCN}=allTags
        if(animationTags[0]!=="All"){
            animationTags.unshift("All")
            animationTagsCN["All"]="全部"
        }
        const type="Animations"
        return (
            <Video type={type} tags={animationTags} tagsCN={animationTagsCN} />
        )
    }
}