import React, { Component } from 'react'
import axios from "axios"
import pubsub from "pubsub-js"
import {withRouter} from "react-router-dom"
import "./SearchButton.css"


class SearchButton extends Component {
    pressEnter=(event)=>{
        if(event.keyCode===13) {
            this.search()
        }
    }
    search=()=>{
        const {value}=this.inputNode;
        this.inputNode.blur()
        axios.get(`/video/search?keywords=${value}`).then(res=>{
            this.props.history.push("/search")
            pubsub.publish("changeList",res.data)
        })
    }
    render() {
        return (
            <div className="searchButton">
                <div className="searchButton_outer">
                    <input className="searchButton_input" placeholder="输入内容进行搜索" onKeyDown={this.pressEnter} ref={node=>this.inputNode=node} />
                    <span className="searchButton_icon" onClick={this.search}></span>
                </div>
            </div>
        )
    }
}

export default withRouter(SearchButton)