import React, { Component } from 'react'
import Video from '../../components/Video/Video'
// import "./Movie.css"
let  allTags =require("../../allTags")

export default class Movie extends Component {
    render() {
        const {movieTags, movieTagsCN}=allTags
        if(movieTags[0]!=="All"){
            movieTags.unshift("All")
            movieTagsCN["All"]="全部"
        }
        const type="Movies"
        return (
            <Video type={type} tags={movieTags} tagsCN={movieTagsCN} />
        )
    }
}
