import React, { Component } from 'react'
import axios from "axios"
import "./Upload.css"
export default class Upload extends Component {
    state={
        percent:0,
        filename:"点击选择要上传的文件",
    }
    upload=()=>{
        this.setState({percent:0})
        const {dir}=this.props
        let that=this
        let formData=new FormData();
        formData.append('type',"file")
        // formData.append('isdir',false)
        formData.append('dir',dir)
        formData.append('FILE',this.fileNode.files[0])
        axios({
            url: '/createFile',
            method: 'post',
            headers: {
                'Content-Type': 'multipart/form-data'
            },
            onUploadProgress: function (progressEvent) { //原生获取上传进度的事件
                if (progressEvent.lengthComputable) {
                    //属性lengthComputable主要表明总共需要完成的工作量和已经完成的工作是否可以被测量
                    //如果lengthComputable为false，就获取不到progressEvent.total和progressEvent.loaded
                    let percent = progressEvent.loaded / progressEvent.total * 100
                    // console.log(percent);
                    that.setState({percent:parseInt(percent)})

                }
            },
            data: formData
        })
        .then(res => {
            if(res.data==="finished"){
                // that.uploaded="成功！"
                that.props.intoDir()
            }
        }).catch(error => {
            console.log(error)
        })
    }
    fakeClick=()=>{
        this.fileNode.click()
    }
    fileChange=()=>{
        if(this.fileNode.files.length>=1){
            this.setState({filename:this.fileNode.files[0].name})
        }else{
            this.setState({filename:"点击选择要上传的文件"})
        }
    }
    render() {
        const {percent}=this.state
        return (
            <div className="upload">
                <div className="upload_outer">
                    <input className="upload_inner" type="file" ref={node=>{this.fileNode=node}} onChange={this.fileChange} />
                    <span className="upload_filename ellipsis"  onClick={this.fakeClick} >{this.state.filename}</span>
                    <span className="upload_percent">{percent>0 ? "--"+percent+"%" : ""}</span>
                    <span className="upload_button" onClick={this.upload} >上传</span>
                </div>
                <span className="cancelButton" onClick={()=>{this.props.toggleUpload(false)}}>关闭</span>
            </div>
        )
    }
}
