import React, { Component } from 'react'
import Video from '../../components/Video/Video'
let  allTags =require("../../allTags")

export default class Tv extends Component {
    render() {
        const {tvTags, tvTagsCN}=allTags
        if(tvTags[0]!=="All"){
            tvTags.unshift("All")
            tvTagsCN["All"]="全部"
        }
        const type="Tvs"
        return (
            <Video type={type} tags={tvTags} tagsCN={tvTagsCN} />
        )
    }
}
