import React, { Component } from 'react'
import pubsub from "pubsub-js"
import List from '../../components/List/List';
import "./Video.css"


export default class Video extends Component {
    state={
        activeIndex:0,//哪个tag需要被高亮
    }
    componentDidMount(){
        const {type ,tags}=this.props
        pubsub.publish("getVideoList",{type,tag:tags[0]})
    }
    clickTab=(index)=>{
        this.setState({activeIndex:index})
        const {type ,tags}=this.props
        pubsub.publish("getVideoList",{type,tag:tags[index]})
    }
    render() {
        const {activeIndex}=this.state
        const {type ,tags, tagsCN}=this.props
        return (
            <div className="videoWraper">
                <div className="tagNav">
                    {
                        tags.map((tag, index) => {
                            return (
                                <span
                                key={index} 
                                onClick={() => { this.clickTab(index) }} 
                                className={index === activeIndex ? "tag tagOn" : "tag"} 
                                >
                                    {tagsCN[tag]}
                                </span>)
                        })
                    }
                </div>
                <div className="videoMain">
                    <List tag={tags[activeIndex]} type={type} />
                </div>
            </div>
        )
    }
}
