import React, { Component } from 'react'
import "./PlayPage.css"
import VideoPlayer from "../../components/VideoPlayer/VideoPlayer"
import ButtonSE from '../../components/ButtonSE/ButtonSE'
import DownLoadList from '../../components/DownLoadList/DownLoadList'

export default class PlayPage extends Component {
    render() {
        let videoInfo=this.props.location.state
        if(!videoInfo){
            this.props.history.push("/movie")
        }else{
            return (
                <div className="playpage">
                    <h1 className="topVideoName">{videoInfo.bundleName}</h1>
                    <VideoPlayer videoInfo={videoInfo} />
                    <ButtonSE videoInfo={videoInfo} />
                    <DownLoadList videoInfo={videoInfo} />
                </div>
            )
        }

    }
}
