import React, { Component } from 'react'
import {withRouter} from "react-router-dom"
import pubsub from "pubsub-js"
import "./SearchList.css"

class SearchList extends Component {
    state={
        videoList:[]
    }
    changeList=(_,videoList)=>{
        this.setState({videoList})
    }
    intoPlayer=(content)=>{
        const {_id:id, bundleSize, type}=content
        this.props.history.push("/playpage",{id,type,bundleSize})
    }
    componentDidMount(){
        pubsub.subscribe("changeList",this.changeList)
    }
    componentWillUnmount(){
        pubsub.unsubscribe("changeList")
    }
    render() {
        const {videoList}=this.state
        let typesCN={
            "Movies":"电影",
            "Animations":"动漫",
            "Tvs":"电视剧",
        }
        return (
            <ul className="search_list" >
                {
                    videoList.length===0 ? 
                    (<p className="search_list_none">没有相关信息</p>):
                    videoList.map(content=>{
                        return(
                            <li className="search_list_item" onClick={()=>{this.intoPlayer(content)}} key={content._id}>
                                <span className="search_result_name">
                                    {content.bundleName}
                                </span>
                                <span className="search_result_type">
                                    {typesCN[content.type]}
                                </span>
                            </li>
                        )
                    })
                    
                }

            </ul>
        )
    }
}

export default withRouter(SearchList)
