import React, { Component } from 'react'
import "./VjsMsg.css"

export default class VjsMsg extends Component {
    state={
        msgShow:false,
        msg:"",
    }
    vjsMsg=(msg)=>{
        if(!this.vcount){
            console.log(this.vcount);
            this.vcount=1
            this.vmark=1
            this.setState({msgShow:true,msg})
            this.interval=setInterval(()=>{
                if(this.vmark===this.vcount){
                    clearInterval(this.interval)
                    this.setState({msg:"",msgShow:false})
                    console.log("clear");
                    this.vcount=0
                }else{
                    this.vmark=this.vcount
                }
            },500)
        }else{
            console.log("update");
            this.vcount++
            this.setState({msgShow:true,msg})
        }
    }
    shouldComponentUpdate(_, nextState){//nextProps
        const {msg, msgShow} = this.state
        if(nextState.msgShow!==msgShow || nextState.msg!==msg){
            return true
        }else{
            return false
        }
    }
    render() {
        const {msgShow, msg}=this.state
        return (
            <div className="vjsMsg" style={{display:msgShow ? "block" : "none"}}>
                {msg}
            </div>
        )
    }
}
