import React, { Component } from 'react'
import videojs from 'video.js'
import 'video.js/dist/video-js.css'
import "./VideoPlayer.css"
import "videojs-playlist/dist/videojs-playlist.es"
import pubsub from "pubsub-js"
// // eslint-disable-next-line
// import vjsEpisodeList from "./EpisodeList/EpisodeList"
// eslint-disable-next-line
import vjsEpisodeButton from "./EpisodeButton/EpisodeButton"
// eslint-disable-next-line
import vjsNextEpisode from "./NextEpisode/NextEpisode"
import VjsMsg from "./VjsMsg/VjsMsg"

export default class VideoPlayer extends Component {
    state = {
        s: 1,
    }
    create_allplaylist = () => {
        let allplaylist = []
        let { bundleSize, id, type } = this.props.videoInfo
        let bundleKeys = Object.keys(bundleSize)
        bundleKeys.forEach((key, index) => {
            let list = []
            for (let i = 0; i < bundleSize[key] * 1; i++) {
                let item = {
                    sources: [{
                        src: `/video/getvideo?id=${id}&type=${type}&s=${index + 1}&e=${i + 1}`,
                        type: 'video/mp4',
                    }]
                }
                list.push(item)
            }
            allplaylist.push(list)
        });
        return allplaylist
    }
    startep = (_, data) => {
        const { s } = this.state
        if (data.s !== s) {
            this.setState({ s: data.s })
        }
        if (this.player && this.player.playlist) {
            if(data.e!==(this.player.playlist.currentItem()+1) || data.s!==s){
                this.player.playlist.currentItem(data.e - 1)
            }
        }
    }
    previousep=()=>{
        let s=this.state.s
        let e=this.player.playlist.currentItem() + 1
        if(e>1){
            let data={
                s,
                e:e-1,
            }
            this.startep(null,data)
        }
        
    }
    nextep=()=>{
        let s=this.state.s
        let { bundleSize } = this.props.videoInfo
        let maxe=bundleSize[Object.keys(bundleSize)[s-1]]
        let e=this.player.playlist.currentItem() + 1
        if(e<maxe){
            let data={
                s,
                e:e+1,
            }
            this.startep(null,data)
        }
        
    }
    addSubs=(s,e)=>{
        let { id, type } = this.props.videoInfo
        this.currentsubs=this.player.addRemoteTextTrack({
            src: `/video/getsubs?id=${id}&type=${type}&s=${s}&e=${e}`,
            default:true,
            label:"中文",
            mode: 'showing',
        },true)
    }
    togglePlay=(e)=>{
        if(this.player.paused()){
            this.player.play()
        }else{
            this.player.pause()
        }
    }
    handlescroll=(event)=>{
        if(this.player.isFullscreen()){
            let operator=(event.nativeEvent.deltaY<0) ? 1 : -1
            this.changevolume(operator)
        }
    }
    changevolume=(operator)=>{
        let newvolume=this.player.volume()+0.05*operator
        if(newvolume>1){
            newvolume=1
        }else if(newvolume<0){
            newvolume=0
        }
        this.player.volume(newvolume)
        this.vjsMsgNode.vjsMsg(`音量${parseInt(newvolume*100)}%`)
    }
    hotkeypress=(event)=>{
        event.preventDefault()
        const hotkey=event.keyCode
        const {player}=this
        if(hotkey===32){
            //空格按下
            if(player.paused()){
                player.play()
            }else{
                player.pause()
            }
        }else if(hotkey===13){
            //enter按下
            if(player.isFullscreen()){
                player.exitFullscreen()
            }else{
                player.requestFullscreen()
            }
        }else if(hotkey===37){
            //向左
            let newtime=player.currentTime()-5
            newtime=newtime>0 ? newtime : 0
            player.currentTime(newtime)
        }else if(hotkey===39){
            //向右
            let newtime=player.currentTime()+15
            newtime=newtime>0 ? newtime : 0
            player.currentTime(newtime)
        }else if(hotkey===38){
            //向上
            this.changevolume(1)
        }else if(hotkey===40){
            //向下
            this.changevolume(-1)
        }else if(hotkey===33){
            //pageup
            this.previousep()
        }else if(hotkey===34){
            //upgedown
            this.nextep()
        }
    }
    componentDidMount() {
        let that = this
        // let { id, type } = this.props.videoInfo
        this.allplaylist = this.create_allplaylist()
        let { s } = this.state
        const {addSubs , startep, nextep}=this
        let videoJsOptions = {
            autoplay: true,
            controls: true,
            fluid: true,
            // height:"720px",
            preLoad:true,
        }
        this.player = videojs(this.videoNode, videoJsOptions, function onPlayerReady() {
            this.playlist(that.allplaylist[s - 1])
            this.playlist.autoadvance(0);
            // addSubs(1,1)
            this.on("firstplay", () => {
                let e = this.playlist.currentItem() + 1
                let { s } = that.state
                pubsub.publish("sete", e)
                pubsub.publish("sete_inline", {e, s})
                addSubs(s,e)
                //设置字幕样式
                var settings = this.textTrackSettings;
                settings.setValues({
                    // "backgroundColor": "rgb(66, 54, 54)",
                    "backgroundOpacity": "0",
                    "edgeStyle": "dropshadow",
                });
                settings.updateDisplay();
                //视频获得焦点以便于键盘控制
                that.videoNode.focus()
            })
            pubsub.subscribe("startep", startep)
            this.getChild('controlBar').addChild("vjsEpisodeButton", {
                s,
                startep,
                bundleSize: that.props.videoInfo.bundleSize,
            }, 10);
            this.getChild('controlBar').addChild("vjsNextEpisode", {nextep}, 1);
        });
    }

    componentWillUnmount() {
        pubsub.unsubscribe("startep")
        if (this.player) {
            this.player.dispose()
        }
        
    }
    render() {
        if (this.player && this.player.playlist) {
            this.player.playlist(this.allplaylist[this.state.s - 1])
        }
        return (
            <div className="vjs-wraper" onKeyDown={this.hotkeypress}>
                <div data-vjs-player >
                    <VjsMsg ref={node=>this.vjsMsgNode=node} />
                    <video 
                    ref={node => this.videoNode = node} 
                    className="video-js"
                    onWheel= {this.handlescroll}
                    ></video>
                </div>
            </div>

        )
    }
}


