import React, { Component } from 'react'
import axios from "axios"
// import {createAndDownloadFile} from "../../utils/download"
import "./Files.css"
import CreateDir from "./CreateDir/CreateDir"
import Upload from "../../components/Upload/Upload"
import MessageBox from "../../components/MessageBox/MessageBox"

export default class File extends Component {
    state={
        currentList:[],
        showCreateDir:false,
        showUpload:false,
        showMsg:false,
    }
    componentDidMount(){
        document.documentElement.addEventListener("keydown",this.handleKeyDown)
        document.documentElement.addEventListener("keyup",this.handleKeyUp)
        document.documentElement.addEventListener("mousedown",this.handleMouseDown)
        this.ctrlDown=false
        this.shiftDown=false
        this.altDown=false
        this.chosenList=[]
        this.moveList=[]
        this.dir="File0"
        this.dirName="根目录"
        this.stack=[this.dir]
        this.stackName=[this.dirName]
        this.intoDir()

    }
    componentWillUnmount(){
        document.documentElement.removeEventListener("keydown",this.handleKeyDown)
        document.documentElement.removeEventListener("keyup",this.handleKeyUp)
        document.documentElement.removeEventListener("mousedown",this.handleMouseDown)
    }
    intoDir=()=>{
        axios.get(`/getfiles?dir=${this.dir}`).then(this.changeList)
    }
    changeList=res=>{
        let currentList=[]
        let currentDirs=[]
        let currentFiles=[]
        res.data.forEach(file=>{
            file.chosen=false
            if(file.type==="dir"){
                currentDirs.push(file)
            }else if(file.type==="file"){
                currentFiles.push(file)
            }
        })
        currentList=[...currentDirs, ...currentFiles]
        this.setState({
            currentList
        })
    }
    handleKeyDown=(event)=>{
        // event.stopPropagation()
        switch (event.keyCode) {
            case 17://ctrl
                if(!this.ctrlDown){
                    this.ctrlDown=true
                }
                break;
            case 16://shift
                if(!this.shiftDown){
                    this.shiftDown=true
                }
                break;
            case 65://a
                event.preventDefault()
                if(this.ctrlDown){
                    let currentList=this.state.currentList.map(item=>{
                        item.chosen=true
                        return item
                    })
                    this.chosenList=currentList
                    this.setState({currentList})
                }
                break
            case 88://x
                if(this.ctrlDown && this.chosenList.length>0){
                    this.moveList=this.chosenList
                    this.fromdir=this.dir
                }
                break
            case 86://v
                if(this.ctrlDown && this.moveList.length>0){
                    const {moveList, fromdir, dir}=this
                    const ids=moveList.map(item=>item._id)
                    let data={ids,dir:fromdir,newdir:dir}
                    this.moveList=[]
                    axios.post("/movefile",data).then(res=>{
                        if(res.data==="success"){
                            this.intoDir()
                        }
                    })
                }
                break
            case 67://c
                if(this.ctrlDown){
                    this.showMessage()
                }
                break
            case 18://alt
                if(!this.altDown){
                    this.altDown=true
                }
                break
            case 46://delete
                const {chosenList, dir}=this
                if(chosenList.length>0){
                    const ids=chosenList.map(item=>item._id)
                    let data={ids,dir}
                    // console.log(data);
                    this.chosenList=[]
                    axios.post("/deletefile",data).then(res=>{
                        if(res.data==="success"){
                            this.intoDir()
                        }
                    })
                }
                
                break
            case 38://up
                if(this.altDown){
                    this.gotoPrevDir()
                }else{
                    if(this.chosenList.length===0){
                        //暂时不添加更多方向按键选择功能
                    }
                }
                break
            case 40://down
                break
            case 37://left
                break
            case 39://right
                break
            default:
                break;
        }

        console.log(event.keyCode);
    }
    handleKeyUp=(event)=>{
        event.stopPropagation()
        event.preventDefault()
        switch (event.keyCode) {
            case 17:
                this.ctrlDown=false
                break;
            case 16:
                this.shiftDown=false
                break
            case 18:
                this.altDown=false
                break;
            default:
                break;
        }
    }
    handleMouseDown=(event)=>{
        if(this.shiftDown){
            event.stopPropagation()
            event.preventDefault()
        }
    }
    handleWarperClick=(event)=>{
        event.stopPropagation()
        this.unselectChosen()
    }
    unselectChosen=()=>{
        this.chosenList=[]
        let currentList=this.state.currentList.map(item=>{
            item.chosen=false
            return item
        })
        this.setState({currentList})
    }
    handleItemkeyDown=(current,event)=>{
        if(event.keyCode===13){//enter
            // console.log(this.chosenList);
            this.handleItemDbclick(current)
        }
    }
    handleItemDbclick=(current)=>{
        if(current.type==="dir"){
            this.dir=current.modelName
            this.dirName=current.name
            this.stack.push(this.dir)
            this.stackName.push(this.dirName)
            this.intoDir()
        }

    }
    gotoPrevDir=()=>{
        let len=this.stack.length
        if(len>1){
            this.dir=this.stack[len-2]
            this.dirName=this.stackName[len-2]
            this.stack.pop()
            this.stackName.pop()
            this.intoDir()
        }
    }

    handleItemClik=(current, event)=>{
        event.stopPropagation()
        // event.preventDefault()
        const toggleItem=item=>{
            if(this.ctrlDown===false){
                item.chosen=false
            }
            if(item._id===current._id){
                item.chosen=!item.chosen
                //判断item是否在chosenList中，如果在删除，不在添加
                let index=this.chosenList.findIndex(chosen=>{
                    if(chosen._id===item._id){
                        return true
                    }else{
                        return false
                    }
                })
                if(index>=0){
                    this.chosenList.splice(index,1)
                }else{
                    this.chosenList.push(item)
                }
            }
            return item
        }
        if(this.ctrlDown){
            let currentList=this.state.currentList
            currentList.map(toggleItem)
            this.setState({currentList})
        }else if(this.shiftDown && this.chosenList.length>0){
            let currentList=this.state.currentList
            let firstIndex=currentList.findIndex(item=>item._id===this.chosenList[this.chosenList.length-1]._id)
            let lastIndex=currentList.findIndex(item=>item._id===current._id)
            if(firstIndex>lastIndex){
                let tempIndex=lastIndex
                lastIndex=firstIndex
                firstIndex=tempIndex
            }
            let newcurrentList=this.state.currentList.map((item,index)=>{
                if(index>=firstIndex && index<=lastIndex){
                    item.chosen=true
                }else{
                    item.chosen=false
                }
                return item
            })
            this.setState({currentList:newcurrentList})
            this.chosenList=newcurrentList.slice(firstIndex,lastIndex+1)
        }else if(this.ctrlDown===false){
            this.chosenList=[]
            let currentList=this.state.currentList.map(toggleItem)
            this.setState({currentList})
        }


    }
    // fileDownLoad=(current,event)=>{
    //     event.stopPropagation()
    //     event.preventDefault()
    //     createAndDownloadFile(current.name,`/downloadfile?id=${current._id}&dir=${current.dir}`)
    // }
    createNewDir=(name)=>{
        if(name==="") return
        axios.get(`/createdir?name=${name}&dir=${this.dir}`).then(res=>{
            if(res.data==="success"){
                this.intoDir()
            }
        })
    }
    showNewDir=()=>{
        this.setState({showCreateDir:true})
    }
    cancelNewDir=()=>{
        this.setState({showCreateDir:false})
    }
    toggleUpload=(showUpload)=>{
        this.setState({showUpload})
    }
    showMessage=()=>{
        this.setState({showMsg:true})
        let timer=setTimeout(()=>{
            this.setState({showMsg:false})
            clearTimeout(timer)
        },1000)
    }
    gotoDir=(index)=>{
        this.dir=this.stack[index]
        this.dirName=this.stackName[index]
        this.stack=this.stack.slice(0,index+1)
        this.stackName=this.stackName.slice(0,index+1)
        this.intoDir()
    }
    render() {
        const { currentList }=this.state
        return (
            <div className="fileWarper" onClick={this.handleWarperClick} >
                {
                    this.state.showCreateDir ? <CreateDir createNewDir={this.createNewDir} cancelNewDir={this.cancelNewDir} currentList={this.state.currentList} /> : ""
                }
                {
                    this.state.showUpload ? <Upload dir={this.dir} toggleUpload={this.toggleUpload} intoDir={this.intoDir} /> : ""
                }
                {
                    this.state.showMsg ? <MessageBox msg="没有复制功能" /> : ""
                }
                <h1 className="dirs">
                    {
                        this.stackName ?
                        this.stackName.map((dirName,index)=>{
                            return(
                                <span  key={index}>
                                    <span className="tagName filetag_left" onClick={()=>{this.gotoDir(index)}} >{dirName}</span>
                                    <span className="filetag_left">&gt;</span>
                                </span>
                            )
                        }):
                        ""
                    }
                    <span className="tagName filetag_right" onClick={this.gotoPrevDir}>上一级</span>
                    <span className="tagName filetag_right" onClick={this.showNewDir} >新建</span>
                    <span className="tagName filetag_right" onClick={()=>{this.toggleUpload(true)}} >上传</span>
                </h1>
                <ul>
                {
                    currentList.map((current)=>{
                        return (
                            <li 
                            className={ current.chosen ? "file_li file_li_active" : "file_li"} 
                            key={current._id} 
                            onClick={(event)=>{this.handleItemClik(current,event)}}
                            onDoubleClick={()=>{this.handleItemDbclick(current)}}
                            tabIndex="-1"
                            onKeyDown={(event)=>{this.handleItemkeyDown(current,event)}}
                            >
                                <span className={ current.type==="dir" ? "dirImg" : "fileImg" }></span>
                                <span className="fileName ellipsis">
                                    {current.name}
                                </span>
                                {
                                    // <span 
                                    // className="fileDownLoad" 
                                    // style={{display:current.type==="file" ? "block" : "none"}}
                                    // onClick={(event)=>{this.fileDownLoad(current,event)}}
                                    // >下载</span>
                                    <a 
                                    className="fileDownLoad" 
                                    download={current.name}
                                    href={`/downloadfile?id=${current._id}&dir=${current.dir}`}
                                    style={{display:current.type==="file" ? "block" : "none"}}
                                    >下载</a>
                                }
                            </li>
                        )
                    })
                    
                }
                </ul>
            </div>
        )
    }
}
