import React, { Component } from 'react'

import "./DownLoadList.css"

export default class DownLoadList extends Component {
    state = {
        s_swift: 1,
    }
    changeSeason = (index) => {
        this.setState({ s_swift: index + 1 })
    }
    create_s_e = () => {
        let { type, bundleSize } = this.props.videoInfo
        const seasonCN = ["第一季", "第二季", "第三季", "第四季", "第五季", "第六季", "第七季", "第八季", "第九季", "第十季", "第十一季", "第十二季", "第十三季", "第十四季", "第十五季"]
        const partCN = ["第一部", "第二部", "第三部", "第四部", "第五部", "第六部", "第七部", "第八部", "第九部", "第十部", "第十一部", "第十二部", "第十三部", "第十四部", "第十五部"]
        let s_name = []
        let s_e = []
        let bundleKeys = Object.keys(bundleSize)
        if (bundleKeys.length === 1) {
            let list = []
            for (let i = 0; i < bundleSize[bundleKeys[0]]; i++) {
                if (type === "Movies") {
                    list.push(partCN[i])
                } else {
                    list.push(i + 1)
                }
            }
            s_e.push(list)
        } else {
            bundleKeys.forEach((key, index) => {
                s_name.push(seasonCN[index])
                let list = []
                for (let i = 0; i < bundleSize[key] * 1; i++) {
                    list.push(i + 1)
                }
                s_e.push(list)
            });
        }
        this.s_e = s_e
        this.s_name = s_name
    }

    createEpisode = (season, index1) => {
        const { id, type, bundleName } = this.props.videoInfo
        const { s_swift } = this.state
        return (

            <ul
                key={index1}
                style={{ "display": ((s_swift === (index1 + 1)) ? "block" : "none") }}
            >
                {
                    season.map((ep, index2) => (
                        <li className="download_ep" key={index2}>
                            <span className="download_ep_name"
                            >{bundleName + "_S" + (index1 + 1) + "_E" + (index2 + 1)}</span>
                            <a 
                            className="download_ep_button" 
                            href={`/video/getvideo?id=${id}&s=${index1 + 1}&e=${index2 + 1}&type=${type}`} 
                            download={`${bundleName}_S${index1 + 1}_E${ index2<9 ? ("0"+(index2+1)) : (index2+1) }`}
                            >下载</a>
                        </li>

                    ))
                }
            </ul>
        )
    }
    render() {
        this.create_s_e()
        const { s_swift } = this.state
        return (
            <div className="dowmload_warper">
                <h1 className="dowmload_header">下载</h1>
                <div className="download_s_wraper" style={{ "display": (this.s_e.length > 1 ? "block" : "none") }}>
                    {this.s_name.map((name, index) => (
                        <button
                            key={index}
                            className={s_swift === (index + 1) ? "download_active_s_button download_season_button" : "download_season_button"}
                            onClick={() => this.changeSeason(index)}
                        >{name}</button>
                    ))}
                </div>
                {this.s_e.map(this.createEpisode)}
            </div>
        )
    }
}
